import { keymap } from 'prosemirror-keymap'
import { Extension } from 'tiptap'
import { redo, undo, yCursorPlugin, ySyncPlugin, yUndoPlugin } from 'y-prosemirror'
import { WebrtcProvider } from 'y-webrtc/src/y-webrtc'
import * as Y from 'yjs'
import './realtime.css'

const ydoc = new Y.Doc()
const provider = new WebrtcProvider(
  'my-crazy-oebl-room-name',
  ydoc,
  // { signaling: ['ws://localhost:4444'] }
)
const type = ydoc.getXmlFragment('prosemirror')

export interface DocumentUsers {
  name: string
  color: string
  colorLight: string
}

function mapToArray<T>(aMap: Map<any, T>): T[] {
  const a: T[] = []
  aMap.forEach(v => {
    a.push(v)
  })
  return a
}

export default class RealtimeExtension extends Extension {
  
  private initialized = false
  
  constructor(opts: {
    user: {
      username: DocumentUsers['name'],
      color: DocumentUsers['color'],
    },
    onUpdateUsers: (us: DocumentUsers[]) => any
    onInit: (us: DocumentUsers[]) => any
  }) {
    super()
    ydoc.on('synced', (e: any) => console.log(e, 'synced'))
    provider.awareness.setLocalStateField('user', {
      name: opts.user.username,
      color: opts.user.color,
      colorLight: opts.user.color
    })
    provider.awareness.on('change', (c: any, b: any) => {
      console.log(provider, b)
      console.log(provider.awareness.getStates())
      if (this.initialized === false) {
        opts.onInit(mapToArray(provider.awareness.getStates()).map((s: any) => s.user))
        this.initialized = true
      }
      if (c.added.length !== 0 || c.removed.length !== 0) {
        opts.onUpdateUsers(mapToArray(provider.awareness.getStates()).map((s: any) => s.user))
      }
    })
  }

  get name () {
    return 'realtime'
  }

  get plugins () {
    return [
      ySyncPlugin(type),
      yCursorPlugin(provider.awareness),
      yUndoPlugin(),
      keymap({
        'Mod-z': undo,
        'Mod-y': redo,
        'Mod-Shift-z': redo
      })
    ]
  }
}
