import { Mark } from 'tiptap'
import { toggleMark, updateMark, createMark } from 'tiptap-commands'
import { MarkSpec } from 'prosemirror-model'
export default class Comments extends Mark {

  get defaultOptions() {
    return {
      user: null
    }
  }

  get name() {
    return 'comments'
  }

  get schema() {
    return {
      attrs: {
        thread: {
          default: [{
            user: 'arni',
            time: new Date(),
            text: 'comment'
          }]
        },
        threadId: {},
      },
      content: 'inline*',
      group: 'block',
      draggable: false,
      parseDOM: [{
        tag: 'comment',
        getAttrs(dom: any) {
          return {
            threadId: dom.dataset.threadId,
            thread: JSON.parse(dom.dataset.thread)
          }
        }
      }],
      toDOM(node: any) {
        return ['comment', {
          'data-thread-id': node.attrs.threadId,
          'data-thread': JSON.stringify(node.attrs.thread)
        }, 0]
      }
    }
  }

  commands(args: any) {
    return (attrs: any) => {
      console.log({args})
      const toggled = toggleMark(args.type, attrs)
      return toggled
    }
  }
}
